.home__description {
  align-items: stretch;
  display: flex;
  flex-flow: row wrap;
  height: 250px; }

.home__description__text--wrapper {
  background-color: #faebd7;
  align-items: center;
  display: flex;
  flex-flow: column wrap; }

.home__description__text__image {
  max-width: 100px;
  max-height: 100px;
  width: 100%;
  height: 100%; }

.home__description__additional-text--wrapper {
  background-color: #b996da; }

.home__description__text--wrapper, .home__description__additional-text--wrapper {
  box-sizing: border-box;
  max-width: 250px;
  padding: 25px;
  width: 100%; }
